import React, { Component } from 'react';
import './OptionForm.css';

class OptionForm extends Component {

  render() {
    return (
      <div className="OptionForm" >
        <h4 className="ui header"></h4>
        <a className="ui right top floated orange icon labeled mini button" 
          id={this.props.id}
          data-number={this.props.number}
          onClick={this.props.onDelete} 
          >
          <i className="icon trash" />
          
        </a>
        <div className={"field" + (this.props.title ? "" : " error")}>
          <label> *</label>
          <input 
            type="text" 
            title="option" 
            name="title" 
            id={this.props.id} 
            value={this.props.title} 
            onChange={this.props.onChange} 
            placeholder="" 
          />
        </div>
        <div className="two fields">
          <div className={"field " + (this.props.id ? "" : "error")}>
            <label> *</label>
            <input 
              type="text" 
              title="option" 
              name="id" 
              id={this.props.id} 
              value={this.props.id} 
              onChange={this.props.onChange} 
              placeholder="" 
              autoFocus={this.props.focus} 
              data-number={this.props.number} 
            />
          </div>
          <div className="field">
            <label></label>
            <input 
              type="text" 
              title="option" 
              name="value" 
              id={this.props.id} 
              value={this.props.value} 
              onChange={this.props.onChange} 
              placeholder="" 
            />
          </div>
        </div>
        <hr className="ui divider" />
      </div>
    );

  }

}

export default OptionForm;



